import type { EventEmitter } from '@stencil/core';
import { Component, Element, Event, Host, Method, Prop, State, Watch, h } from '@stencil/core';

@Component({
	tag: 'o-carousel',
	styleUrl: 'o-carousel.scss',
	shadow: true
})
export class OCarousel {
	private carousel: HTMLSlotElement;
	private scrollContainer: HTMLElement;
	private paginationContainer: HTMLElement;
	private prevEnabled = false;
	private nextEnabled = false;

	@Element() host!: HTMLOCarouselElement;

	@Prop() navigation: boolean = false;

	@Prop() pagination: boolean = false;

	@State() activeSlide = 0;

	@State() slides: HTMLOCarouselItemElement[] = [];

	@State() scrolling = false;

	@Event() oSlideChange: EventEmitter<{ index: number; slide: HTMLOCarouselItemElement }>;

	@Method()
	async previous(): Promise<void> {
		if (!this.prevEnabled) return;
		await this.goToSlide(this.activeSlide - 1);
	}

	@Method()
	async next(): Promise<void> {
		if (!this.nextEnabled) return;
		await this.goToSlide(this.activeSlide + 1);
	}

	@Method()
	async goToSlide(index) {
		const slides = this.getSlides();

		if (!slides.length) {
			return;
		}

		const newActiveSlide = this.getClampedValue(index, 0, slides.length - 1);
		this.activeSlide = newActiveSlide;

		const nextSlide = slides[newActiveSlide];
		this.scrollToSlide(nextSlide as unknown as HTMLElement);
	}

	@Watch('activeSlide')
	handleActiveSlideChange() {
		this.setNavigation();

		this.oSlideChange.emit({ index: this.activeSlide, slide: this.slides[this.activeSlide] });
	}

	componentDidLoad() {
		this.scrollToSlide(this.slides[this.activeSlide]);
		this.host.setAttribute('role', 'region');
		this.host.setAttribute('aria-label', 'Carousel');
	}

	handleSlotChange() {
		this.setNavigation();
		this.slides = this.getSlides();
		this.slides.forEach((slide, index) => {
			slide.setAttribute('role', 'group');
			slide.setAttribute('aria-label', `Slide number ${index + 1}`);
			if (this.pagination) {
				slide.setAttribute('id', `slide-${index + 1}`);
				slide.setAttribute('role', 'tabpanel');
				slide.removeAttribute('aria-label');
				slide.setAttribute('aria-labelledby', `tab-${index + 1}`);
			}
		});
	}

	handleKeyDown = async (event: KeyboardEvent) => {
		if (['ArrowLeft', 'ArrowRight', 'ArrowUp', 'ArrowDown', 'Home', 'End'].includes(event.key)) {
			const target = event.target as HTMLElement;
			const focusInPagination = target.closest('button') != null;
			const isNext = event.key === 'ArrowUp' || event.key === 'ArrowRight';
			const isPrevious = event.key === 'ArrowDown' || event.key === 'ArrowLeft';

			event.preventDefault();

			if (isNext) await this.next();

			if (isPrevious) await this.previous();

			if (event.key === 'Home') await this.goToSlide(0);

			if (event.key === 'End') await this.goToSlide(this.getSlides().length - 1);

			if (focusInPagination) {
				const paginationItems = this.paginationContainer.querySelectorAll('button');
				const activePaginationItem = paginationItems[this.activeSlide];

				if (activePaginationItem) {
					activePaginationItem.focus();
				}
			}
		}
	};

	isButton(item: HTMLElement) {
		return item.tagName.toLowerCase() === 'button';
	}

	setNavigation() {
		this.prevEnabled = this.canScrollPrev();
		this.nextEnabled = this.canScrollNext();
	}

	canScrollPrev() {
		return this.activeSlide > 0;
	}

	canScrollNext() {
		return this.activeSlide < this.getSlides().length - 1;
	}

	isCarouselItem(item: HTMLElement) {
		return item.tagName.toLowerCase() === 'o-carousel-item';
	}

	getSlides() {
		return [...this.carousel.assignedElements?.({ flatten: true })].filter((el: HTMLElement) => {
			if (el.inert || !this.isCarouselItem(el)) {
				return false;
			}
			return true;
		}) as unknown as HTMLOCarouselElement[];
	}

	/* istanbul ignore next */
	scrollToSlide(slide: HTMLElement, behavior: ScrollBehavior = 'smooth') {
		window.requestAnimationFrame(() => {
			if (!this.scrollContainer) {
				return;
			}

			const scrollContainer = this.scrollContainer;
			const scrollContainerRect = scrollContainer.getBoundingClientRect();
			const nextSlideRect = slide.getBoundingClientRect();

			const nextLeft = nextSlideRect.left - scrollContainerRect.left;
			const nextTop = nextSlideRect.top - scrollContainerRect.top;

			if (nextLeft || nextTop) {
				scrollContainer.scrollTo({
					left: nextLeft + scrollContainer.scrollLeft,
					top: nextTop + scrollContainer.scrollTop,
					behavior
				});
			}
		});
	}

	getClampedValue(value, min, max) {
		if (value < min) {
			return min;
		}
		if (value > max) {
			return max;
		}
		return value;
	}

	render() {
		return (
			<Host>
				{/* eslint-disable tailwindcss/no-custom-classname --
				 * The carousel class is being used to set the grid template areas as this is not functionality that tailwind supports.
				 * Removing this class will break the carousel's grid functionality.
				 */}
				<div
					class={`carousel grid grid-cols-[min-content_1fr_min-content] grid-rows-[1fr_min-content] items-center min-h-full min-w-full relative py-4 ${this.navigation ? 'gap-x-4' : ''} ${this.pagination ? 'gap-y-4' : ''}`}
				>
					{/* eslint-enable tailwindcss/no-custom-classname */}
					{/* eslint-disable tailwindcss/no-custom-classname --
					 * The slides class is being used to set the grid template area for reasons stated above.
					 * The scrollbar-hidden class is being used to set the scrollbar-width to none as Tailwind as of v4
					 * does not support this functionality. It is also being used to hide the Webkit scrollbar.
					 */}
					<div
						id="scroll-container"
						ref={(el) => (this.scrollContainer = el as HTMLElement)}
						class={
							'slides scrollbar-hidden grid grid-flow-col auto-cols-[100%] auto-rows-[100%] size-full overflow-auto overflow-y-hidden overscroll-x-contain snap-mandatory snap-x relative rounded-radius-lg focus:outline-width-lg focus:-outline-offset-2 focus-visible:outline-width-lg focus:outline-focus-ring-color-border-default focus-visible:ring-focus-ring-color-border-default'
						}
						aria-busy={this.scrolling ? 'true' : 'false'}
						aria-atomic="true"
						onKeyDown={(event) => void this.handleKeyDown(event)}
					>
						{/* eslint-enable tailwindcss/no-custom-classname */}
						<slot
							ref={(el) => (this.carousel = el as HTMLSlotElement)}
							onSlotchange={() => this.handleSlotChange()}
						></slot>
					</div>
					{this.navigation && (
						<div class={'contents'}>
							<o-icon-button
								class={
									'col-start-1 col-end-auto row-start-1 row-end-auto auto-cols-min flex flex-none items-center ml-2'
								}
								name="chevron-left"
								size="md"
								disabled={!this.prevEnabled}
								onClick={() => void this.previous()}
								aria-label="Previous slide"
								aria-controls="scroll-container"
								aria-disabled={this.prevEnabled ? 'false' : 'true'}
							></o-icon-button>
							<o-icon-button
								class={'col-start-3 col-end-auto row-start-1 row-end-auto flex flex-none items-center mr-2'}
								name="chevron-right"
								size="md"
								disabled={!this.nextEnabled}
								onClick={() => void this.next()}
								aria-label="Next slide"
								aria-controls="scroll-container"
								aria-disabled={this.nextEnabled ? 'false' : 'true'}
							></o-icon-button>
						</div>
					)}
					{/* eslint-disable tailwindcss/no-custom-classname --
					 * The carousel class is being used to set the grid template areas as this is not functionality that tailwind supports.
					 * Removing this class will break the carousel's grid functionality.
					 */}
					{this.pagination && (
						<div
							class={'pagination flex flex-wrap row-start-2 col-span-3 gap-2 justify-center'}
							ref={(el) => (this.paginationContainer = el as HTMLElement)}
						>
							{/* eslint-enable tailwindcss/no-custom-classname */}
							{this.slides.map((_, index) => (
								<button
									class={`block cursor-pointer bg-none rounded-full p-0 m-0 ${index === this.activeSlide ? 'bg-background-brand-knockout size-4' : 'bg-background-brand border-width-sm border-border-default size-3.5'} focus:outline-width-lg focus:-outline-offset-0 focus-visible:outline-width-lg focus:outline-focus-ring-color-border-default focus-visible:ring-focus-ring-color-border-default`}
									onClick={() => void this.goToSlide(index)}
									aria-controls={`slide-${index + 1}`}
									aria-label={`${index === this.activeSlide ? 'Slide number ' + (index + 1) : 'Go to Slide ' + (index + 1)}`}
									tabIndex={index === this.activeSlide ? 0 : -1}
									onKeyDown={(event) => void this.handleKeyDown(event)}
								></button>
							))}
						</div>
					)}
				</div>
			</Host>
		);
	}
}
